<template>
  <div class="mining_bg">
    <el-row>
      <el-col :span="24">
        <div style="height: 130px;"></div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div style="display:flex;">
          <div style="width: 22%;margin: 0%;"></div>
          <div style="width: 37%;margin: 0%;"><p class="text_base text_app_title">{{$t('mining.citizen_independent')}}</p>
            <p class="text_base text_app_content">{{$t('mining.citizen_independent1')}}
</p>
            <p class="text_base text_app_content">{{$t('mining.citizen_independent2')}}
</p>
            <p class="text_base text_app_content">{{$t('mining.citizen_independent3')}}
</p>
            <p class="text_base text_app_content">{{$t('mining.citizen_independent4')}}</p>
            <p class="text_base text_citizen_content1">{{$t('mining.citizen_independent5')}}</p>
            <a :href="getCitizenGuidePc()"><p class="text_base text_question">{{$t('mining.citizen_guide_pc')}}&nbsp;&nbsp;&nbsp;&nbsp;<img src="/static/img/miner_enter.png"/></p></a>
          </div>
          <div style="width: 20%;" class="miner_pic"><img width="390px" height="273px" src="/static/img/miner.png"/></div>
          <div style="width: 20%;"></div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div style="display:flex;">
          <div style="width:20%;"></div>
          <div><img width="269px" height="190px" v-bind:src="getCitizenPicture(1)" class="citizen_pic"/><p class="text_base text_content">{{$t('mining.citizen_sign_up')}}</p></div>
          <div style="width:5%;"><img src="/static/img/miner_step.png" class="citizen_step"/></div>
          <div><img width="336px" height="190px" v-bind:src="getCitizenPicture(2)" class="citizen_pic"/><p class="text_base text_content">{{$t('mining.citizen_register')}}</p></div>
          <div style="width:5%;"><img src="/static/img/miner_step.png" class="citizen_step"/></div>
          <div><img width="336px" height="190px" v-bind:src="getCitizenPicture(3)" class="citizen_pic"/><p class="text_base text_content">{{$t('mining.citizen_invite')}}</p></div>
          <div><p class="text_base text_content"></p></div>
        </div>
      </el-col>
    </el-row>
    <el-row>
        <el-col>
            <div style="height:325px;"></div>
        </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div style="display:flex;">
          <div style="width:22%;"></div>
          <div style="width:20%;"><img width="337px" height="372px" src="/static/img/mining_mobile.png"/></div>
          <div style="width:36%;padding-left:3%;padding-right:5%;"><p class="text_base text_app_title">{{$t('mining.user_guide')}}</p>
            <p class="text_base text_app_content" style="line-height: 34px;">{{$t('mining.user_guide_detail1')}}</p>

            <p class="text_base text_app_content" style="margin-top:50px;">{{$t('mining.user_guide_detail2')}}</p>
            <a :href="getMinerGuidePc()"><p class="text_base text_question">{{$t('mining.user_guide_mobile')}}&nbsp;&nbsp;&nbsp;&nbsp;<img src="/static/img/miner_enter.png"/></p></a>
          </div>
          <div style="width:10%;"></div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
      <div style="display:flex;">
        <div style="width:19%;"><p class="text_base text_title"></p></div>
        <div><img class="user_pic" width="233px" height="412px" v-bind:src="getMinerPicture(1)"/><p class="text_base text_content">{{$t('mining.download_anybit')}}</p></div>
        <div style="width:4.6%;"><img class="user_step" src="/static/img/miner_step.png"/></div>
        <div><img class="user_pic" width="233px" height="412px" v-bind:src="getMinerPicture(2)"/><p class="text_base text_content">{{$t('mining.user_register')}}</p></div>
        <div style="width:4.6%;"><img class="user_step" src="/static/img/miner_step.png"/></div>
        <div><img class="user_pic" width="233px" height="412px" v-bind:src="getMinerPicture(3)"/><p class="text_base text_content">{{$t('mining.choose_citizen')}}</p></div>
        <div style="width:4.6%;"><img class="user_step" src="/static/img/miner_step.png"/></div>
        <div><img class="user_pic" width="233px" height="412px" v-bind:src="getMinerPicture(4)"/><p class="text_base text_content">{{$t('mining.weighted_vote')}}</p></div>
        <div><p class="text_base text_content"></p></div>
      </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      activeIndex: '1',
      theme: 'dark'
    }
  },
  mounted () {
  },
  methods: {
    getCitizenGuidePc () {
      return this.$t('mining.pc_guide_url')
    },
    getMinerGuidePc () {
      return this.$t('mining.miner_guide_url')
    },
    getMinerPicture (index) {
      return this.$t('mining.miner' + index)
    },
    getCitizenPicture (index) {
      return this.$t('mining.citizen' + index)
    }
  }
}
</script>
<style scoped>
.mining_bg {
  width: 100%;
  height: 2187px;
  background-image:url("/static/img/miner_bg.png");
}
.text_base {
  color: snow;
  text-align: center;
  font-size: 18pt;
}
.text_content {
  font-size: 20px;
  line-height: 10px;
}
.text_app_title {
  font-size: 36px;
  font-weight: bold;
  line-height: 9px;
  text-align: left;
}
.text_app_content {
  font-size: 14px;
  text-align: left;
  margin-right: 0%;
  line-height: 22px;
}
.text_citizen_content1 {
  font-size: 14px;
  text-align: left;
  margin-right: 0%;
  margin-top: 50px;
  line-height: 20px;
}
.text_question {
  font-size: 20px;
  color: #35D2FF;
  text-align: left;
  line-height: 40px;
}
.hr {
  margin-bottom: 40px;
}
.miner_pic {
  text-align: left;
  padding-top: 50px;
}
.citizen_pic {
  margin-top: 50px;
}
.citizen_step {
  margin-top: 130px;
}
.user_pic {
  margin-top: 100px;
}
.user_step {
  margin-top: 300px;
}
a:link,a:visited{
 text-decoration:none;
}
a:hover{
 text-decoration:none;
}
</style>



